.Contact {
    margin-top: 5rem;
    background-color: #f1f1f1ea;
}

.Contact .jumbotron {
    height: 95vh;
    background: url(https://www.w3schools.com/w3images/mac.jpg);
    
}

.Contact button {
    margin: auto;
    display: block;
    width: 90%;
}

.Contact .showcase h1 {
    color: #fff;
    text-align: center;
    margin-top: 0;
    padding: 1em;
    font-size: 5em;
    font-weight: 400;
}

.Contact .showcase h2{
    color: #fff;
    font-weight: 300;
    text-align: center;
    margin-left: 2em;
}

.Contact .showcase {
    background: rgba(0, 0, 0, 0.767);
    height: 95vh;
}

.socials {
    background-color: #cecbcb67;
}

.socials ul {
    padding: 0;
    margin: 0;
}

.socials li {
    list-style: none;
    margin: 2rem;
    margin-top: 0;
    padding: 10px;
    font-size: 1.4em;
}

.Contact h3 {
    text-align: center;
    font-weight: 400;
    font-size: 2em;
}

.contact-form {
    background-image: url('../../img/img6.jpg');
    background-size: cover;
    background-position: center;

}

.contact-form .layer {
    background-color: #f0f0f0d0;
}

.contact-form input, textarea {
    width: 100%;
    padding: 1rem;
    margin: 10px auto;
    border: 1px solid #fffffff8;
    background: transparent;
    border-radius: 10px;
}

.contact-form form {
    width: 70%;
    margin: auto;
}

.contact-form input,textarea {
    box-shadow: 0 2px 5px 0 rgba(53, 53, 53, 0.63),
    0 2px 10px 0 rgba(105, 104, 104, 0.637);;
}





