.Explore {
    margin-top: 4em;
    background-color: #f1f5f5;
}

.Explore .grid {
    /* margin: 1.4rem auto; */
    width: 80%;
    margin: 2rem;
    margin-left: 4.7rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.Explore video {
    object-fit: cover;
    width: 100%;
    height: 95vh;
}

.Explore .video-showcase {
    position: relative;
}

.Explore .showcase {
    object-fit: contain;
}

.Explore .hero {
    color: #fff;
    position: absolute;
    top: 15%;
}

.Explore .hero h1 {
    text-align: center;
    font-size: 3em;
    margin-bottom: 2em;
}

.Explore .hero h2 {
    text-align: center;
    margin-left: 2em;
}

.Explore .hero h3 {
    margin-left: 3em;
    margin-top: 3em;
}

.Explore .container .drop-down-container {
    width: 80%;
    margin: 2rem auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.Explore .container select {
    margin: auto;
    width: 100%;
    padding: 0.8rem;
    border: 1px solid rgba(156, 154, 154, 0.849);
    border-radius: 10px;
    width: 100%;
}


.Explore .container .input input {
    padding: 0.8rem;
    margin:  auto;
    border: 1px solid rgba(156, 154, 154, 0.849);
    border-radius: 10px;
    width: 100%;
    transition: cubic-bezier(0.165, 0.84, 0.44, 1) 200ms;
}

.Explore .container .input input:focus {
    box-shadow: 0 2px 5px 0 rgba(49, 76, 167, 0.411),
    0 2px 10px 0 rgba(42, 69, 94, 0.384);
    outline: none;
}

.Explore .container .input {
    margin: auto;
}

.Explore .container h2 {
    text-align: center;
    font-size: 3.5em;
}

.Explore .btn-outline-warning {
    margin: 3rem;
    margin-top: 2rem;
    display: block;
    text-align: center;
    width: 30%;
}

.Explore hr {
     background-image: linear-gradient(to right, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
     border: 0;
     height: 1px;
}

@media screen and (max-width: 1000px) {
    .Explore .grid {
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 10px;
        margin: 1rem 1.2rem;
        margin-left: 2rem;
    }

    .Explore {
        margin-top: 0;
        overflow: hidden;
    }

    .Explore .hero {
        color: #fff;
        position: absolute;
        top: 10%;
    }

    .Explore .container .drop-down-container {
        grid-template-columns: 1fr 1fr;
    }

    .Explore .container .drop-down-container input {
        margin-bottom: 2rem;
        margin-top: 1rem;
    }
}

@media screen and (max-width: 1200px) {
    .Explore .grid {
        grid-gap: 20px;
    }
}

@media screen and (min-width: 1122px) {
    .Explore .grid .grid-container {
        margin: auto;
        grid-gap: 10px;
    }
}
@media screen and (min-width: 1279px) {
    .Explore .grid {
        margin-left: 4rem;
        grid-gap: 10px;
    }
}
@media screen and (min-width: 1059px) {
    .Explore .grid {
        margin-right: 8rem;
        margin-left: 0;
        grid-gap: 10px;
    }
}

@media screen and (max-width: 615px) {
    .Explore .grid {
        display: block;
    }

    .Explore {
        margin-top: 0;
        overflow: hidden;
    }

    .Explore .hero {
        color: #fff;
        position: absolute;
        top: 10%;
    }

    .Explore .container .drop-down-container {
        grid-template-columns: 1fr 1fr;
    }

    .Explore .container .drop-down-container input {
        margin-bottom: 2rem;
        margin-top: 1rem;
    }
}

@media screen and (min-width: 1100px) {
    .Explore .grid {
        margin-left: 1rem;
    }
}
@media screen and (min-width: 1238px) {
    .Explore .grid {
        margin-left: 4rem;
    }
}



